import React from 'react';
import { graphql } from 'gatsby';
import styled from 'styled-components';

import Layout from '../components/layout';
import SEO from '../components/seo';

import Header from '../containers/header';

import Setup from '../sections/setup';
import CustomSpeed from '../sections/custom-speed';
import CustomHeight from '../sections/custom-height';
import DisableAgents from '../sections/disable-agents';
import Directions from '../sections/directions';
import EffectTypes from '../sections/effect-types';
import CustomElements from '../sections/custom-elements';

import { HomePageQuery } from '../../graphql-types';

interface Props {
  readonly data: HomePageQuery;
}

const Main = styled.main``;

const HomePage: React.FC<Props> = ({ data }) => {
  const { title, description } = data.site.siteMetadata;

  return (
    <Layout>
      <SEO title={title} description={description} />

      <Header title={title} />

      <Main>
        <Setup />
        <CustomSpeed />
        <CustomHeight />
        <DisableAgents />
        <Directions />
        <EffectTypes />
        <CustomElements />
      </Main>
    </Layout>
  );
};

export const query = graphql`
  query HomePage {
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`;

export default HomePage;
