import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { faTachometerAlt } from '@fortawesome/free-solid-svg-icons';
import ReactParlx from 'react-parlx';

import Preview from '../containers/preview';

import { CustomHeightQuery } from '../../graphql-types';

const CustomSpeed: React.FC = () => {
  const data = useStaticQuery<CustomHeightQuery>(query);

  return (
    <>
      <Preview data={data.file} icon={faTachometerAlt} />

      <ReactParlx className="parallax" overlay>
        <img
          // className="parlx-children"
          // src={sanFrancisco}
          alt="san-francisco"
        />
        <h3 className="parallax-marker">0.3</h3>
      </ReactParlx>
    </>
  );
};

export const query = graphql`
  query CustomSpeed {
    file(name: { eq: "custom-speed" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }
  }
`;

export default CustomSpeed;
