import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import {
  faArrowCircleDown,
  faPlayCircle,
  faRunning,
  faBoxOpen,
} from '@fortawesome/free-solid-svg-icons';

import Preview from '../containers/preview';

import { SetupQuery } from '../../graphql-types';

const Setup: React.FC = () => {
  const data = useStaticQuery<SetupQuery>(query);

  const { install, start, autoinit, methods } = data;

  return (
    <>
      <Preview data={install} icon={faArrowCircleDown} />
      <Preview data={start} icon={faPlayCircle} />
      <Preview data={autoinit} icon={faRunning} />
      <Preview data={methods} icon={faBoxOpen} />
    </>
  );
};

export const query = graphql`
  query Setup {
    install: file(name: { eq: "how-to-install" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }

    start: file(name: { eq: "getting-started" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }

    autoinit: file(name: { eq: "autoinit" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }

    methods: file(name: { eq: "methods" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }
  }
`;

export default Setup;
