import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { faMapSigns } from '@fortawesome/free-solid-svg-icons';

import Preview from '../containers/preview';

import { EffectTypesQuery } from '../../graphql-types';

const EffectTypes: React.FC = () => {
  const data = useStaticQuery<EffectTypesQuery>(query);

  return (
    <>
      <Preview data={data.file} icon={faMapSigns} />
    </>
  );
};

export const query = graphql`
  query EffectTypes {
    file(name: { eq: "custom-effect-type" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }
  }
`;

export default EffectTypes;
