import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import ReactParlx from 'react-parlx';

import Preview from '../containers/preview';

import { CustomElementsQuery } from '../../graphql-types';

const CustomElements: React.FC = () => {
  const data = useStaticQuery<CustomElementsQuery>(query);

  return (
    <>
      <ReactParlx className="parallax" overlay>
        <video
          className="parlx-children"
          // src={night}
          autoPlay
          playsInline
          loop
          muted
        />
        <h3 className="parallax-marker">Not only images!</h3>
      </ReactParlx>

      <Preview data={data.file} />
    </>
  );
};

export const query = graphql`
  query CustomElements {
    file(name: { eq: "custom-elements" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }
  }
`;

export default CustomElements;
