import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { faRulerVertical } from '@fortawesome/free-solid-svg-icons';

import Preview from '../containers/preview';

import { CustomHeightQuery } from '../../graphql-types';

const CustomHeight: React.FC = () => {
  const data = useStaticQuery<CustomHeightQuery>(query);

  return (
    <>
      <Preview data={data.file} icon={faRulerVertical} />
    </>
  );
};

export const query = graphql`
  query CustomHeight {
    file(name: { eq: "custom-height" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }
  }
`;

export default CustomHeight;
