import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { faDirections } from '@fortawesome/free-solid-svg-icons';
import ReactParlx from 'react-parlx';

import Preview from '../containers/preview';

import { DirectionsQuery } from '../../graphql-types';

const Directions: React.FC = () => {
  const data = useStaticQuery<DirectionsQuery>(query);

  return (
    <>
      <Preview data={data.file} icon={faDirections} />

      <ReactParlx className="parallax" overlay>
        <img
          className="parlx-children"
          //  src={einkorn}
          alt="einkorn"
        />
        <h3 className="parallax-marker">vertical</h3>
      </ReactParlx>

      <ReactParlx
        className="parallax"
        settings={{ direction: 'horizontal' }}
        overlay
      >
        <img
          className="parlx-children"
          // src={india}
          alt="india"
        />
        <h3 className="parallax-marker">horizontal</h3>
      </ReactParlx>

      <ReactParlx
        className="parallax"
        settings={{ direction: 'diagonal' }}
        overlay
      >
        <img
          className="parlx-children"
          // src={satellite}
          alt="satellite"
        />
        <h3 className="parallax-marker">diagonal</h3>
      </ReactParlx>
    </>
  );
};

export const query = graphql`
  query Directions {
    file(name: { eq: "custom-direction" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }
  }
`;

export default Directions;
