import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { faBan } from '@fortawesome/free-solid-svg-icons';

import Preview from '../containers/preview';

import { DisableAgentsQuery } from '../../graphql-types';

const DisableAgents: React.FC = () => {
  const data = useStaticQuery<DisableAgentsQuery>(query);

  return (
    <>
      <Preview data={data.file} icon={faBan} />
    </>
  );
};

export const query = graphql`
  query DisableAgents {
    file(name: { eq: "disable-user-agents" }) {
      childMarkdownRemark {
        frontmatter {
          title
        }
        html
      }
    }
  }
`;

export default DisableAgents;
